.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.title{
  font-size: 50px;
  text-align: center;
  padding: 20px;
}

.outer-div
{
  display: flex;
  justify-content: center;
}

.inner-div{
  margin-bottom: 50px;
}

.choose-story
{
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  margin-bottom: 40px;
}

.social-icons
{
  display: flex;
  justify-content: center;
}

.social-icons:hover
{
  display: flex;
  justify-content: center;
  cursor: pointer;
}

.social-icon-item{
  margin: 10px;
  /* width: 30px; */
}

.flex-column-div{
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  width: 350px;
}

.flex-column{
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
}

.flex-item
{
}

.circle-img{
  border-radius: 50%;
  width: 60px;
  border: 1px solid;
  border-color: grey;
  padding: 3px;
}

.circle-img:hover{
  border-radius: 50%;
  width: 60px;
  border: 1px solid;
  border-color: grey;
  padding: 3px;
  cursor: pointer;
}



.story-title{
  text-align: center;
}

.card-shadow{
  -webkit-filter: drop-shadow(0 10px 10px #888);
  filter: drop-shadow(0 10px 10px #888);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
